import {Moment} from "moment";

export function getDialogDate(calendarFocusDate: Moment, dayInWeek: number, sundayFirst: boolean): any {
    if (window.innerWidth < 768) {
       return calendarFocusDate.format('DD/MM/YYYY')
    }
    if (dayInWeek === 0 && !sundayFirst) {
        return calendarFocusDate.clone().day(dayInWeek).add(7, 'day').format('DD/MM/YYYY')
    }  else {
        return calendarFocusDate.clone().day(dayInWeek).format('DD/MM/YYYY')
    }
    
}

export function getDialogDateForRequest(calendarFocusDate: Moment, dayInWeek: number, sundayFirst: boolean): any {
    if (window.innerWidth < 768) {
        return calendarFocusDate.format('MM/DD/YYYY')
    }
    if (dayInWeek === 0 && !sundayFirst) {
        return calendarFocusDate.clone().day(dayInWeek).add(7, 'day').format('MM/DD/YYYY')
    } else {
        return calendarFocusDate.clone().day(dayInWeek).format('MM/DD/YYYY')
    }
    
}
